 <!--品牌列表-->
<template>
  <div class="cms-list-layout">
    <cms-tabs>
      <cms-tab to="/brand/list">品牌列表</cms-tab>
      <cms-tab to="/brand/audit">待审核品牌</cms-tab>
    </cms-tabs>
    <cms-prompt collapse='true'>
      <li>显示品牌列表信息</li>
      <li>可对品牌列表进行新增、修改、删除操作</li>
    </cms-prompt>
    <!-- 筛选区 -->
    <section class="cms-list-query">
      <div class="query-group">
        <label>品牌名称</label>
        <el-input v-model="queryParams.name" @keyup.native.enter='query'></el-input>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
    </section>
    <!-- 操作区 -->
    <cms-sticky>
    <section class="cms-batch">
      <cms-button  v-prem="'/brand/save'" icon="add" type="success" size="small" @click="$routerLink('/brand/save','save')">新增</cms-button>
      <cms-button  v-prem="'/brand/delete'"  icon="error" type="error" size="small" @click="deleteDatas()">批量删除</cms-button>
    </section>
    </cms-sticky>
    <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems' 
     @row-click='checkRows' ref='dataTable' class="brands-form">
      <el-table-column type="selection" width="46" fixed='left'></el-table-column>
      <el-table-column prop="id" label="id" width="60"></el-table-column>
      <el-table-column prop="name" label="品牌名称" min-width="200">
        <template slot-scope="scope">
          <div class="text-overflow" :title="scope.row.name"> {{scope.row.name}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="initials" label="首字母" min-width="150"></el-table-column>
      <el-table-column label="LOGO" min-width="180" prop="logo">
        <div slot-scope="scope" class="table-img">
          <img v-if="scope.row.resourcesSpaceData" :src="$cms.filterUrl(scope.row.resourcesSpaceData.url) " alt="mockimg">
        </div>
      </el-table-column>
      <el-table-column label="排序" prop="sortNum" min-width="150">
        <div slot-scope="scope">
          <el-input v-model="scope.row.sortNum" @keyup.enter.native="sortNum($event,scope.row.id)"></el-input>
        </div>
      </el-table-column>
      <el-table-column prop="show" label="是否显示" min-width="110">
        <div slot-scope="scope">
          <cms-switch v-model="scope.row.show" :id='scope.row.id' @change="switchShow(scope.row)"></cms-switch>
        </div>
      </el-table-column>
      <el-table-column label="操作" width='175' fixed="right" align="center">
        <div slot-scope="scope">
          <cms-button v-prem="'/brand/edit'" icon='edit' size='small' type='info' @click.stop="$routerLink('/brand/edit','update',{id:scope.row.id})">编辑</cms-button>
          <cms-button v-prem="'/brand/delete'" icon='error' size='small' type='info' @click="deleteData(scope.row.id)">删除</cms-button>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
  </div>
</template>
<script>
import list from '@/mixins/list'
import { brandList, brandDelete, brandSort, brandUpdate } from '@/api/product/brand.js'
var defaultData = {
  page: 1,
  size: 10,
  name: '',
  status: 4,
  queryType: 1
}
export default {
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData)
    }
  },
  methods: {
    deleteData(id) {
      // 包装一层删除的操作
      this.deleteTableData(brandDelete, id) // 这个才是真实的删除方法
    },
    deleteDatas() {
      this.deleteTableDatas(brandDelete, this.ids) // 这个才是真实的删除方法
    },
    switchShow(item) { // 滑块的事件
      brandUpdate(item).then(res => {
        if (res.code === 200) {
          this.$successMessage('修改成功')
          this.getTableData(brandList, this.queryParams)
        } else {
          this.$errorMessage(res.code)
        }
      })
    },
    sortNum(el, id) {
      // 数字排序
      const val = el.target.value
      const reg = /^[1-9][0-9]{0,1}$/
      if (val === '') {
        return false
      } else {
        if (reg.test(val)) {
          el.target.className = 'el-input__inner'
          brandSort({ sorts: [{
            id: id,
            sortNum: val
          }] }).then(res => {
            if (res.code === 200) {
              this.$successMessage('修改成功')
            } else {
              this.$errorMessage('修改失败')
            }
          })
        } else {
          el.target.className = 'cms-error el-input__inner'
          this.$cmsMessage({
            message: '只能输入1-99的正整数',
            type: 'error',
            timer: '1000',
            fastClick: false
          })
        }
      }
    }
  },
  created() {
    // 首次触发
    this.getTableData(brandList, this.queryParams)
  }
}
</script>
<style>
</style>



